body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
  width: 450px;
  margin: 80px auto;
  border: 3px solid cadetblue;
  padding: 20px 20px 20px 20px;
}
.date {
  background-color: cadetblue;
  position: relative;
  top: -30px;
  width: 308px;
  height: 30px;
  color: antiquewhite;
  align-items: center;
}
h1 {
  position: relative;
  top: -20px;
}
input {
  width: 300px;
  height: 50px;
  font-size: 40px;
}

select {
  float: right;
  width: 120px;
  height: 50px;
  margin-top: -75px;
}

.selectRu {
  width: 120px;
  height: 50px;
  margin-top: -10px;
  float: right;
  text-align: left;
  font-size: 50px;
  font-weight: bold;
}

span {
  color: black;
}
